import axios from 'axios'
import { useState, useEffect } from 'react'

import '../styles/Product.css'
import { API_URL, BUCKET } from '../utils/Variables'

const Product = props => {
    const [qty, setQty] = useState(1)
    const [selectedImage, setSelectedImage] = useState(null)
    const [images, setImages] = useState([])
    const [product, setProduct] = useState([])


    useEffect(() => {
        console.log("Using Effect - Slug.js")

        axios.get(`${API_URL}/products/${props.slug}/`).then(response => {
            let imgs = []

            response.data.images.forEach((img, idx) => {
                imgs.push(<img src={`${BUCKET}/product_images/${product.slug}/${img}`} className='productImages' alt='product' width="100%" /> )
            })
    

            setImages(imgs)
            setSelectedImage(imgs[0])
            setProduct(response.data)
        }).catch(error => console.log(error))
    }, [product.name, product.slug, props.slug])


    const adjustQty = direction => {
        if (direction === 'up') {
            setQty(qty + 1)
        } else {
            if (qty > 1) {
                setQty(qty - 1)
            }
        }
    }

    return(
        <div className="ProductContainer">
            <h1 className="ProductName">{product.name}</h1>

            <div className="ProductImagesContainer">
                <div className="SelectedProductImage">
                    { selectedImage }
                </div>

                <div className="ThumbnailsContainer">
                    { 
                        images.map((image, idx) => (
                                    <div 
                                        className="ThumbnailImageContainer" 
                                        key={idx}
                                        onClick={() => setSelectedImage(image)}
                                    >
                                        {image}
                                    </div>

                        ))
                    }
                </div>

            </div>

            <div className="ProductInfoContainer">
                <p className="ProductDescription">{product.description}</p>
                
                <h3>${product.price}</h3>

                <div className="QtyAdjustmentContainer">
                    <span className="QtyAdjustmentButton" onClick={() => adjustQty('down')}>&#8610;</span>
                    <h2 className="QtyLabel">qty: <span>{qty}</span></h2>
                    <span className="QtyAdjustmentButton" onClick={() => adjustQty('up')}>&#8611;</span> 
                </div>

                <button className="Button" onClick={() => props.addItemToCart(product, qty)}>add to cart</button>                
            </div>

        </div>
    )
}

export default Product